@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap');

body {
  font-family: 'Noto Sans JP', sans-serif;
  background-color: #f6f6f6;
  font-size: 18px;
  padding: 10px;
}

.playerProfileComputer {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.loadingData {
  text-align: center;
}

.spinner.loading {
  border: none;
  position: relative;
  text-align: center;
}

.spinner.loading:before {
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  border-width: 4px;
  border-style: solid;
  border-color: #2180c0 #fff #fff;
  border-radius: 100%;
  animation: rotation .7s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  } to {
    transform: rotate(359deg);
  }
}

#playerSearchID, .web-content button {
  display: inline-block;
}

.canvases {
  text-align: center;
}
.canvasChart {
  display: inline-block;
  width: 40%;
  text-align: center;  
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: white;
  padding-bottom: 20px;
}
.canvasClass {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}

table {  
  background-color: white;
  border-collapse: collapse;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: auto;
  margin-bottom: auto;
}

table th, table td {
  border: solid black 1px;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  min-width: 80px;
  text-transform: uppercase;
}

